import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DatatableComponent } from './datatable/datatable.component';
import { TypographyComponent } from './typography/typography.component';
import { ButtonComponent } from './button/button.component';
import { CardComponent } from './card/card.component';
import { GridListComponent } from './grid-list/grid-list.component';
import { IconComponent } from './icon/icon.component';
import { ListComponent } from './list/list.component';
import { MenuComponent } from './menu/menu.component';
import { PanelComponent } from './panel/panel.component';
import { ProgressSpinnerComponent } from './progress-spinner/progress-spinner.component';
import { QrScannerComponent } from 'angular2-qrscanner';
import { QrScanComponent } from './qr-scan/qr-scan.component';
import { QrScannerResultComponent } from './qr-scanner/qr-scanner.component'


const routes: Routes = [
  { path: '', pathMatch: 'full' , component: DatatableComponent },
  { path: 'typography', component: TypographyComponent },
  { path: 'button', component: ButtonComponent },
  { path: 'card', component: CardComponent },
  { path: 'gridlist', component: GridListComponent },
  { path: 'icon', component: IconComponent },
  { path: 'list', component: ListComponent },
  { path: 'menu', component: MenuComponent },
  { path: 'panel', component: PanelComponent },
  { path: 'spinner', component: ProgressSpinnerComponent },
  { path: 'qrscanner', component: QrScanComponent },
  { path: 'qrscannerresult', component:  QrScannerResultComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
