import { Component, OnInit, ViewChild } from '@angular/core';
import { QrScannerComponent } from 'angular2-qrscanner';
import { QRREsultService } from '../qrresult.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-qr-scan',
  templateUrl: './qr-scan.component.html',
  styleUrls: ['./qr-scan.component.css']
})
export class QrScanComponent implements OnInit {
  constructor(private _QRResultService : QRREsultService, private _router: Router) { }


  @ViewChild(QrScannerComponent, { static: true }) qrScannerComponent: QrScannerComponent;

  devices: any = [];
  selectedDevice: any;

  ngOnInit(): void {
    this.qrScannerComponent.getMediaDevices().then(devices => {
      for (const device of devices) {
        if (device.kind.toString() === 'videoinput') {
          this.devices.push(device);
        }
      }
      console.log('🚀 ~ this.qrScannerComponent.getMediaDevices ~  this.devices:',  this.devices);
      // if (this.devices.length > 0) {
      //   let choosenDev;
      //   for (const dev of this.devices) {
      //     if (dev.label.includes('front')) {
      //       choosenDev = dev;
      //       break;
      //     }
      //   }
      //   if (choosenDev) {
      //     this.qrScannerComponent.chooseCamera.next(choosenDev);
      //   } else {
      //     this.qrScannerComponent.chooseCamera.next(videoDevices[0]);
      //   }
      // }
    });

    this.qrScannerComponent.capturedQr.subscribe(result => {
      this.navigateRouter(result);
      console.log("result : ", result);
    });
  }

  opened = false;

  log(state) {
    console.log("state : ", state)
  }

  navigateRouter(result) {
    this._QRResultService.setResult(result);
    this._router.navigate(['qrscannerresult']);
  }

  onChange(value) {
    this.selectedDevice = value;
    this.qrScannerComponent.chooseCamera.next(this.selectedDevice);
  }


}
