import { Component, OnInit } from '@angular/core';
import { QRREsultService } from '../qrresult.service';

@Component({
  selector: 'app-qr-scanner',
  templateUrl: './qr-scanner.component.html',
  styleUrls: ['./qr-scanner.component.css']
})
export class QrScannerResultComponent implements OnInit {

  constructor(private _qrResultService: QRREsultService) { }
  result: string = "";
  ngOnInit(): void {
    this.result = this._qrResultService.getResult();
  }

}
